d6a0bec>2019.03 - 2021.08</p></div><div class=\"row-right\" data-v-6d6a0bec><h3 data-v-6d6a0bec>개발 설계 구축</h3><ul data-v-6d6a0bec><li data-v-6d6a0bec>사이트 리뉴얼 의뢰</li><li data-v-6d6a0bec>프레임워크 선정 및 ERP 구조 설계, 리뉴얼 사이트 구축 계획</li></ul></div></section></article><article class=\"skills\" data-v-6d6a0bec><h1 data-v-6d6a0bec>Skills<em data-v-6d6a0bec>.</em></h1><section data-v-6d6a0bec><div class=\"row-center\" data-v-6d6a0bec><h2 data-v-6d6a0bec>Communication<em data-v-6d6a0bec>.</em></h2><ul data-v-6d6a0bec><li data-v-6d6a0bec><h4 data-v-6d6a0bec> 모르는 기술이 있더라도 필요하다면 능동적으로 찾아보고 학습하여 업무에 적극적으로 활용합니다. </h4></li><li data-v-6d6a0bec><h4 data-v-6d6a0bec>커머스 플랫폼에 대한 폭넓은 이해와 운영 경험</h4></li><li data-v-6d6a0bec><h4 data-v-6d6a0bec>남들과 다른 미친 적응력과 생존력</h4></li></ul></div></section><section data-v-6d6a0bec><div class=\"row-center\" data-v-6d6a0bec><h2 data-v-6d6a0bec>WebSkil<em data-v-6d6a0bec>.</em></h2><ul data-v-6d6a0bec><li data-v-6d6a0bec><h4 data-v-6d6a0bec>Java, Spring, Jsp, mySql, Oracle</h4><p data-v-6d6a0bec> · LF트라이씨클에서 근무하면서, 이커머스 특성 상 대용량 트래픽을 다뤄보는 경험을 쌓았습니다. 이를 통해 자바와 스프링 등의 기초적인 백엔드 업무를 전문적으로 수행해왔습니다. </p><p data-v-6d6a0bec> · LF트라이씨클에서 프론트업무와 동시에 서버까지 데이터를 저장하는 풀스택개발 경험을 했었습니다 </p><p data-v-6d6a0bec> · KH정보교육원에서는 기초적인 MVC 패턴을 기반으로 한 CRUD 및 JSP을 통한 웹 개발 흐름을 습득하고 학습하였습니다. </p></li><li data-v-6d6a0bec><h4 data-v-6d6a0bec>AngularJS, Vue, typescript</h4><p data-v-6d6a0bec> · 주로 Angular를 사용하여 트라이씨클 자사몰(하프클럽, 보리보리)의 모바일 웹과 PC영역들을 개발했었습니다. </p><p data-v-6d6a0bec> · 자사인트라넷 Bo에서 Vue를 통한 신규게시판 기능들을 개발하였으며 버그들을 수정해왔습니다. </p><p data-v-6d6a0bec> · 개인적인 실무경험들과 공부들을 통하여 프론트개발의 기술을 키우고자 타입스크립트를 주로 공부하였습니다. </p></li><li data-v-6d6a0bec><h4 data-v-6d6a0bec>Jenkins, aws</h4><p data-v-6d6a0bec> · 트라이씨클에서 주된 서버배포용으로 사용하였으며 개발 및 운영서버를 나누어 주된 빌드와 배포를 하였습니다. </p><p data-v-6d6a0bec> · 학원프로젝트 진행시 포트폴리오 사이트 공개를 위해 aws 사이트 배포를 경험했었습니다. </p></li><li data-v-6d6a0bec><h4 data-v-6d6a0bec>SCSS, css, Vuetify, bootstrap</h4><p data-v-6d6a0bec> · 삼성카드 모니모앱 프로젝트에 퍼블리셔로서 scss 와 뷰티파이를 사용한 경험이있습니다. </p><p data-v-6d6a0bec> · KH정보교육원에서는 정적인 html영역과 그를 채워주는 css에 대해서 기초적인 역량을 배웠으며 부트스트랩을 통한 라이브러리 활용경험을 배웠었습니다. </p></li></ul></div></section><section data-v-6d6a0bec><div class=\"row-center\" data-v-6d6a0bec><h2 data-v-6d6a0bec>Collaboration tool<em data-v-6d6a0bec>.</em></h2><ul data-v-6d6a0bec><li data-v-6d6a0bec><h4 data-v-6d6a0bec>GitHub, GitLab, Git, bitbucket</h4><p data-v-6d6a0bec> · 학원 및 개인적인 프로젝트와 공부등을 위해 git과 github를 주로 사용하여 깃을 활용한 협업에 능숙합니다. </p><p data-v-6d6a0bec> · 트라이씨클 및 삼성카드 모니모 프로젝트를 깃랩과 빗버킷을 활용하여 팀원들과 협업을 진행하였습니다. </p></li><li data-v-6d6a0bec><h4 data-v-6d6a0bec>Jira, redmine, zeplin</h4><p data-v-6d6a0bec> · 트라이씨클에서 개발자 및 MD 들과 소통을 하기위해 레드마인을 사용하였으며 삼성카드 모니모에선 디자이너와 현업들과 소통하기 위해 지라를 사용했습니다. </p><p data-v-6d6a0bec> · 삼성카드 모니모 레이아웃을 구성하기 위해 디자이너들과 협업을 위한 제플린을 직접 이용하여 px 값등을 확인하여 완벽한 ui/ux를 구성하기 위해 작업했었습니다. </p></li></ul></div></section></article>", 2)
